.containerContactForm {
  display: flex;
  height: 553px;
  flex-direction: column;
  align-items: center;
  gap: 23px;
  flex: 1 0 0;
}
.containerFieldEmail {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 18px;
  align-self: stretch;
}
.fieldEmailLabel {
  color: var(--blue-main-accent-color);
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}
.fieldEmailInput {
  height: 60px;
  align-self: stretch;
  border-radius: 4px;
  border: 1px solid var(--blue-main-accent-color);
  background: rgba(255, 255, 255, 0.5);
  outline: none;
}
.containerFieldName {
  display: flex;
  align-items: flex-start;
  gap: 18px;
  align-self: stretch;
}
.fieldName {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 18px;
  flex: 1 0 0;
}
.fieldNameLabel {
  color: var(--blue-main-accent-color);
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}
.fieldNameInput {
  height: 60px;
  align-self: stretch;
  border-radius: 4px;
  border: 1px solid var(--blue-main-accent-color);
  background: rgba(255, 255, 255, 0.5);
  outline: none;
}
.containerFieldMessage {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: 18px;
  align-self: stretch;
}
.fieldMessageLabel {
  color: var(--blue-main-accent-color);
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}
.fieldMessageInput {
  height: 182px;
  align-self: stretch;
  border-radius: 4px;
  border: 1px solid var(--blue-main-accent-color);
  background: rgba(255, 255, 255, 0.5);
  outline: none;
}
.buttonFormSubmit {
  display: flex;
  height: 60px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 8px;
  flex-shrink: 0;
  align-self: stretch;
  background: var(--blue-main-accent-color);
  color: #fff;
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  border: 0;
}
.activeField {
  border: 2px solid var(--gold-main-accent-color);
  background: #fff;
}
