.accordionSection {
  display: flex;
  flex-direction: column;
  align-items: center;
  align-self: stretch;
  background: #ffffff;
}
.accordionHeaderContainer {
  display: flex;
  height: 95px;
  padding: 0px 24px;
  align-items: center;
  gap: 24px;
  align-self: stretch;
  background: var(--gold-main-accent-color);
  cursor: pointer;
  box-shadow: -5px -5px 5px -5px #bcbaba;
}
.accordionHeader {
  display: flex;
  padding-right: 0px;
  align-items: center;
  gap: 24px;
  flex: 1 0 0;
}
.accordionTitleIcon {
  width: 10rem;
  height: 10rem;
  padding-top: 2rem;
}
.accordionTitle {
  color: #fff;
  text-align: center;
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  text-transform: uppercase;
}
.accordionArrow {
  color: #fff;
  text-align: center;
  font-family: Inter;
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  text-transform: uppercase;
}
.open {
  transform: rotate(90deg);
  transition: transform 0.7s ease-in-out;
}
.notOpen {
  transform: rotate(0deg);
  transition: transform 0.7s ease-in-out;
}
.accordionContentSection {
  display: flex;

  padding: 24px;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  gap: 24px;
  align-self: stretch;
  overflow: hidden;
}

.accordionContentImage {
  display: flex;
  width: 227px;
  height: 228px;

  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 8px;
}
.left {
  padding: 1px 200px;
}
.right {
  padding: 1px 200px 1px 50px;
}
@media screen and (min-width: 992px) and (max-width: 1352px) {
  .left {
    padding: 1px 40px;
  }
  .right {
    padding: 1px 40px;
  }
}
@media screen and (max-width: 991px) {
  .accordionHeaderContainer {
    height: 44px;
    justify-content: space-between;
    padding: 0px 16px;
  }
  .accordionTitleIcon {
    display: none;
  }
  .accordionTitle {
    font-size: 14px;
  }
  .accordionContentSection {
    padding: 16px;
    flex-direction: column;
    gap: 16px;
    align-self: stretch;
  }
  .accordionContentImage {
    height: 149px;
    padding: 1px 35px;

    align-self: stretch;
  }
  .accordionContentDescText {
    font-size: 14px !important;
  }
  .accordionSubHeader {
    height: 44px !important;
    padding: 0px 16px !important;
  }
  .accordionContentSubSection {
    padding: 16px !important;
    gap: 16px !important;
    width: 89% !important;
  }

  .accordionContentSubSectionDesc {
    flex-direction: column !important;
    align-items: center !important;
  }
}
.accordionContentDesc {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 8px;
  flex: 1 0 0;
  align-self: stretch;
}
.accordionContentDescText {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex: 1 0 0;
  align-self: stretch;
  color: #00040a;

  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}
.accordionSubHeader {
  display: flex;
  height: 95px;
  padding: 0px 24px;
  align-items: center;
  gap: 24px;
  align-self: stretch;
  cursor: pointer;

  /*transition: all 0.3s ease;*/
  justify-content: space-between;
  background: var(--gold-main-accent-color);
}
.slide-down-subSection {
  max-height: max-content !important;
  opacity: 1 !important;
  /*padding: 24px;
  animation-name: slideDown;
    animation: slideDown 1s ease-in-out;*/
}
.accordionContentSubSection {
  display: flex;
  padding: 24px;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: 24px;
  align-self: stretch;
  width: 100%;
}
.accordionContentSubSectionDesc {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  gap: 8px;
  flex: 1 0 0;
  align-self: stretch;
  justify-content: space-around;
  overflow: hidden;
}
