.scene {
  min-width: 20rem;
  height: 26.7rem;
  perspective: 600px;
  margin: 1rem;
}
.card {
  width: 100%;
  height: 100%;
  position: relative;
  transition: transform 1s;
  transform-style: preserve-3d;
}
.face {
  position: absolute;
  height: 100%;
  width: 100%;
  backface-visibility: hidden;
}
.face.back {
  transform: rotateY(180deg);
}
.card.is-flipped {
  transform: rotateY(180deg);
}

@media screen and (max-width: 991px) {
  .scene {
    min-width: 14.5rem;
    height: 19.3rem;
  }
}
