.container {
  display: flex;
  /*width: 1427px;
  height: 2130px;*/
  padding: 0px 148px;
  flex-direction: column;
  align-items: center;
  position: relative;
  overflow: hidden;
  background-image: url(/public/image/Union.svg);
  background-repeat: no-repeat;
}
@media screen and (max-width: 1352px) {
  .container {
    background-image: none;
  }
}
.footerApp {
  display: flex;
  position: relative;
  /*bottom: 0;*/
  height: 200px;
  background: var(--blue-main-accent-color);
}
.unionBottom {
  position: absolute;
  bottom: 9rem;
  right: 0rem;
  z-index: -1;
  height: 50rem;
}

@media screen and (min-width: 992px) and (max-width: 1352px) {
  .container {
    padding: 0px 86px 0px 80px;
  }
  .unionBottom {
    display: none;
  }
}
@media screen and (max-width: 991px) {
  .container {
    padding: 0px;
  }
  .unionBottom {
    display: none;
  }
}
