.containerContact {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: 24px;
  align-self: stretch;
}
.headerContact {
  color: var(--blue-main-accent-color);
  font-size: 49px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}
.insideContainer {
  height: 286px;
}
.inside {
  height: 60px;
}
.containerForm {
  display: flex;
  align-items: flex-start;
  gap: 25px;
  align-self: stretch;
}
@media screen and (max-width: 1352px) {
  .insideContainer {
    display: flex;
    height: 286px;
  }
}
@media screen and (max-width: 1200px) {
  .containerForm {
    display: flex;
    align-items: stretch;
    flex-direction: column;
    gap: 25px;
    align-self: stretch;
    padding: 0 10px;
  }
}
